.about {
	position: relative;
	padding-top: 80px;

	@media (min-width: $screen-sm-min) {
		padding-top: 0;
	}

	@media (min-width: $screen-md-min) {
		padding-bottom: $grid-gutter-width*2;
	}

	&__heading {
		position: relative;
		margin: 0;
		z-index: 10;
		top: $grid-gutter-width;
		font-size: 74px;

		@media (min-width: $screen-sm-min) {
			position: absolute;
			font-size: 100px;
		}

		@media (min-width: $screen-md-min) {
			position: relative;
			margin-top: $grid-gutter-width;
			font-size: 120px;
		}
	}

	&__images {
		@media (min-width: $screen-sm-min) {
			margin-top: 20px;
		}

		@media (min-width: $screen-md-min) {
			margin-top: -50px;
			margin-bottom: -250px;
		}

		img {
			width: 100%;
		}
	}

	&__image-bg {
		overflow: hidden;
		display: none;
		@media (min-width: $screen-md-min) {
			display: inline-block;
			width: 100%;
			max-width: 300px;
			position: absolute;
			left: 50%;
			top: -200px;
			z-index: -1;
		}
	}

	&__image-me {
		overflow: hidden;
		width: 100%;
		max-width: 100%;
		margin-bottom: 2*$grid-gutter-width;
		@media (min-width: $screen-md-min) {
			max-width: 370px;
			margin-left: 18%;
		}
	}

	&__left-col {
		@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
			margin-top: -250px;
		}
	}

	&__text {
		margin-bottom: 2*$grid-gutter-width;
		line-height: 2.1em;
		p {
			margin-bottom: 2.1em;
		}

		@media (min-width: $screen-lg-min) {
			margin-left: $grid-gutter-width*.75;
		}
	}

	&__contact {
		margin-bottom: 2*$grid-gutter-width;
		position: relative;
		display: inline-block;
		font-size: 20px;
		color: $gray-base;
		font-weight: 700;
		font-family: $headings-font-family;
		padding-bottom: 4px;
		z-index: 10;
		&:hover, &:focus {
			text-decoration: none;
			color: $gray-base;
			&:after {
				max-width: 0;
			}
		}
		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			max-width: 100%;
			height: 2px;
			background-color: $gray-base;
			transition: ease 400ms all;
		}
		@media (min-width: $screen-md-min) {
			position: relative;
			top: -100px;
		}
	}

	&__list-abs {
		@media (min-width: $screen-md-min) {
			position: absolute;
			top: 156px;
			left: 250px;
		}
	}

	&__lists {
		@media (min-width: $screen-sm-min) {
			position: relative;
		}

		@media (min-width: $screen-md-min) {
			top: -27px;
		}
	}
}
